*, *::before, *::after {
    box-sizing: border-box;
}

* {
    margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
    html {
    interpolate-size: allow-keywords;
  }
}

body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
}

input, button, textarea, select {
    font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
}

p {
    text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
    text-wrap: balance;
}

#root, #__next {
    isolation: isolate;
}

body {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

header,
footer {
    padding: 10px 0;
    background-color: var(--color-1);
    color: white;
}

.title {
    text-align: center;
    font-family: "Flavors", cursive;
    font-weight: 400;
    font-style: normal;
    font-size: clamp(3rem, 4vw, 6rem);
    color: var(--color-5);
}

.made-by {
    text-align: center;
}

main {
    position: relative;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}

main.game-over {
    position: relative;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dialog {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 5%;
    border-radius: 16px;
}

.dialog > button {
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    width: clamp(200px, 25%, 320px);
    margin: 4%;
    padding: 10px;
    border: 3px solid var(--color-2);
    border-radius: 20px;
    background-color: var(--color-1);
    transition: transform 300ms ease-out;
}

.dialog button img {
    width: 30%;
}

.dialog > button:hover {
    cursor: pointer;
    transform: scale(1.05);
    background-color: var(--color-2);
}

.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.top-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.top-container > h2 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: clamp(1rem, 3vw, 3rem);
    color: var(--color-5);
}

.top-container h2 img {
    width: clamp(2.5rem, 5vw, 5rem);
}

.top-btns {
    width: 100%;
    display: flex;
    justify-content: center;
}

.top-btns button {
    color: var(--color-5);
    padding: 4px 16px;
    margin: 5px;
    border: 1px solid var(--color-2);
    border-radius: 10px;
    background-color: var(--color-1);
    transition: transform 300ms ease-out;
}

.drag-btn.unclickable {
    background-color: var(--color-4);
}

.bottom-container {
    display: flex;
    align-items: stretch;
}

.ready-btn {
    color: var(--color-5);
    padding: 10px 30px;
    border: 2px solid var(--color-2);
    border-radius: 10px;
    background-color: var(--color-1);
    transition: transform 300ms ease-out;
}

.top-container button:hover,
.ready-btn:hover {
    cursor: pointer;
    transform: scale(1.05);
    background-color: var(--color-2);
}

.boards {
    display: flex;
}

.player1,
.player2 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.player-icon {
    width: 5vw;
}

.board1,
.board2,
.board-init {
    position: relative;
    display: grid;
    grid: repeat(10, 1fr) / repeat(10, 1fr);
    width: 28vw;
    margin: 20px;
    padding: 8px;
    border: 2px solid var(--color-4);
    border-radius: 10px;
}

.box {
    aspect-ratio: 1 / 1;
    background-color: var(--color-5);
    border: 1px dotted black;
}

.drag {
    display: flex;
    flex-direction: column;
    width: 18vw;
    margin: 20px;
    padding: 8px;
    border: 2px solid var(--color-4);
    border-radius: 10px;
}

.orientation-btns {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 4px;
}

.orientation-btns button {
    display: flex;
    justify-content: center;
    color: var(--color-5);
    padding: 2px 8px;
    border: 1px solid var(--color-2);
    border-radius: 10px;
    background-color: var(--color-1);
    transition: transform 300ms ease-out;
}

.orientation-btns button:hover {
    cursor: pointer;
    background-color: var(--color-2);
}

.orientation-btns button img {
    width: 30%;
}

.drag-ships {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 8%;
    border-radius: 10px;
    background-color: var(--color-5);
    background-image: 
    linear-gradient(to right, #ccc 1px, transparent 1px),
    linear-gradient(to bottom, #ccc 1px, transparent 1px);
    background-size: 35px 35px;
}

.carrier.horizontal {
    height: 11%;
    width: 95%;
    margin-bottom: 8%;
    background-color: var(--color-1);
}

.carrier.vertical {
    height: 60%;
    width: 18%;
    margin-right: 4%;
    background-color: var(--color-1);
}

.battleship.horizontal {
    height: 11%;
    width: 76%;
    margin-bottom: 8%;
    background-color: var(--color-1);
}

.battleship.vertical {
    height: 48%;
    width: 18%;
    margin-right: 4%;
    background-color: var(--color-1);
}

.destroyer.horizontal,
.submarine.horizontal {
    height: 11%;
    width: 57%;
    margin-bottom: 8%;
    background-color: var(--color-1);
}

.destroyer.vertical,
.submarine.vertical {
    height: 36%;
    width: 18%;
    margin-right: 4%;
    background-color: var(--color-1);
}

.patrol-boat.horizontal {
    height: 11%;
    width: 38%;
    margin-bottom: 8%;
    background-color: var(--color-1);
}

.patrol-boat.vertical {
    height: 24%;
    width: 18%;
    background-color: var(--color-1);
}

.hover-enabled:hover {
    cursor: crosshair;
    background-color: var(--color-4);
}

.unclickable {
    pointer-events: none;
}

.ship1,
.ship2 {
    background-color: var(--color-1);
}

.box.miss {
    background-color: yellow;
}

.box.hit {
    background-color: red;
}

.invisible {
    background-color: white;
}

.sunk-msg {
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    color: var(--color-5);
    background-color: var(--color-1);
    font-size: 1rem;
    padding: 10px;
    border: 1px solid var(--color-2);
    border-radius: 10px;
}

.sunk-msg img {
    width: clamp(25px, 5vw, 45px);
}

.changeover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.changeover button,
.handoff-btn,
.new-game-btn {
    display: flex;
    color: var(--color-5);
    padding: 2px 8px;
    border: 1px solid var(--color-2);
    border-radius: 10px;
    background-color: var(--color-1);
    transition: transform 300ms ease-out;
}

.changeover button,
.handoff-btn div {
    display: flex;
    align-items: center;
}

.changeover button img,
.handoff-btn img {
    width: clamp(25px, 5vw, 45px);
}

.changeover button:hover,
.handoff-btn:hover,
.new-game-btn:hover {
    cursor: pointer;
    transform: scale(1.05);
    background-color: var(--color-2);
}

.gg {
    margin: 50px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--color-5);
    font-size: 2rem;
}

.gg div:nth-child(1) {
    font-size: 2.5rem;
}

.gg div:nth-child(2) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    color: var(--color-5);
    background-color: var(--color-1);
    font-size: 2rem;
    padding: 10px;
}

.gg div:nth-child(2) img {
    width: clamp(25px, 5vw, 45px);
}

.gg + button {
    color: var(--color-5);
    padding: 10px 30px;
    border: 1px solid var(--color-2);
    border-radius: 10px;
    background-color: var(--color-1);
    font-size: 1.5rem;
    transition: transform 300ms ease-out;
}

.gg + button:hover {
    cursor: pointer;
    transform: scale(1.05);
    background-color: var(--color-2);
}

footer a {
    color: var(--color-5);
