./img/layered-waves-2.svg');\n    background-size: cover;\n    background-position: center;\n    background-repeat: no-repeat;\n    flex-grow: 1;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.dialog {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    align-items: center;\n    padding: 5%;\n    border-radius: 16px;\n}\n\n.dialog > button {\n    display: flex;\n    justify-content: center;\n    font-size: 1.2rem;\n    width: clamp(200px, 25%, 320px);\n    margin: 4%;\n    padding: 10px;\n    border: 3px solid var(--color-2);\n    border-radius: 20px;\n    background-color: var(--color-1);\n    transition: transform 300ms ease-out;\n}\n\n.dialog button img {\n    width: 30%;\n}\n\n.dialog > button:hover {\n    cursor: pointer;\n    transform: scale(1.05);\n    background-color: var(--color-2);\n}\n\n.main-container {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n.top-container {\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n.top-container > h2 {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: clamp(1rem, 3vw, 3rem);\n    color: var(--color-5);\n}\n\n.top-container h2 img {\n    width: clamp(2.5rem, 5vw, 5rem);\n}\n\n.top-btns {\n    width: 100%;\n    display: flex;\n    justify-content: center;\n}\n\n.top-btns button {\n    color: var(--color-5);\n    padding: 4px 16px;\n    margin: 5px;\n    border: 1px solid var(--color-2);\n    border-radius: 10px;\n    background-color: var(--color-1);\n    transition: transform 300ms ease-out;\n}\n\n.drag-btn.unclickable {\n    background-color: var(--color-4);\n}\n\n.bottom-container {\n    display: flex;\n    align-items: stretch;\n}\n\n.ready-btn {\n    color: var(--color-5);\n    padding: 10px 30px;\n    border: 2px solid var(--color-2);\n    border-radius: 10px;\n    background-color: var(--color-1);\n    transition: transform 300ms ease-out;\n}\n\n.top-container button:hover,\n.ready-btn:hover {\n    cursor: pointer;\n    transform: scale(1.05);\n    background-color: var(--color-2);\n}\n\n.boards {\n    display: flex;\n}\n\n.player1,\n.player2 {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.player-icon {\n    width: 5vw;\n}\n\n.board1,\n.board2,\n.board-init {\n    position: relative;\n    display: grid;\n    grid: repeat(10, 1fr) / repeat(10, 1fr);\n    width: 28vw;\n    margin: 20px;\n    padding: 8px;\n    border: 2px solid var(--color-4);\n    border-radius: 10px;\n}\n\n.box {\n    aspect-ratio: 1 / 1;\n    background-color: var(--color-5);\n    border: 1px dotted black;\n}\n\n.drag {\n    display: flex;\n    flex-direction: column;\n    width: 18vw;\n    margin: 20px;\n    padding: 8px;\n    border: 2px solid var(--color-4);\n    border-radius: 10px;\n}\n\n.orientation-btns {\n    display: flex;\n    justify-content: space-evenly;\n    align-items: center;\n    padding: 4px;\n}\n\n.orientation-btns button {\n    display: flex;\n    justify-content: center;\n    color: var(--color-5);\n    padding: 2px 8px;\n    border: 1px solid var(--color-2);\n    border-radius: 10px;\n    background-color: var(--color-1);\n    transition: transform 300ms ease-out;\n}\n\n.orientation-btns button:hover {\n    cursor: pointer;\n    background-color: var(--color-2);\n}\n\n.orientation-btns button img {\n    width: 30%;\n}\n\n.drag-ships {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    height: 100%;\n    padding: 8%;\n    border-radius: 10px;\n    background-color: var(--color-5);\n    background-image: \n    linear-gradient(to right, #ccc 1px, transparent 1px),\n    linear-gradient(to bottom, #ccc 1px, transparent 1px);\n    background-size: 35px 35px;\n}\n\n.carrier.horizontal {\n    height: 11%;\n    width: 95%;\n    margin-bottom: 8%;\n    background-color: var(--color-1);\n}\n\n.carrier.vertical {\n    height: 60%;\n    width: 18%;\n    margin-right: 4%;\n    background-color: var(--color-1);\n}\n\n.battleship.horizontal {\n    height: 11%;\n    width: 76%;\n    margin-bottom: 8%;\n    background-color: var(--color-1);\n}\n\n.battleship.vertical {\n    height: 48%;\n    width: 18%;\n    margin-right: 4%;\n    background-color: var(--color-1);\n}\n\n.destroyer.horizontal,\n.submarine.horizontal {\n    height: 11%;\n    width: 57%;\n    margin-bottom: 8%;\n    background-color: var(--color-1);\n}\n\n.destroyer.vertical,\n.submarine.vertical {\n    height: 36%;\n    width: 18%;\n    margin-right: 4%;\n    background-color: var(--color-1);\n}\n\n.patrol-boat.horizontal {\n    height: 11%;\n    width: 38%;\n    margin-bottom: 8%;\n    background-color: var(--color-1);\n}\n\n.patrol-boat.vertical {\n    height: 24%;\n    width: 18%;\n    background-color: var(--color-1);\n}\n\n.hover-enabled:hover {\n    cursor: crosshair;\n    background-color: var(--color-4);\n}\n\n.unclickable {\n    pointer-events: none;\n}\n\n.ship1,\n.ship2 {\n    background-color: var(--color-1);\n}\n\n.box.miss {\n    background-color: yellow;\n}\n\n.box.hit {\n    background-color: red;\n}\n\n.invisible {\n    background-color: white;\n}\n\n.sunk-msg {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: fit-content;\n    color: var(--color-5);\n    background-color: var(--color-1);\n    font-size: 1rem;\n    padding: 10px;\n    border: 1px solid var(--color-2);\n    border-radius: 10px;\n}\n\n.sunk-msg img {\n    width: clamp(25px, 5vw, 45px);\n}\n\n.changeover {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background-image: url('../img/layered-waves.svg');\n    background-size: cover;\n    background-position: center;\n    background-repeat: no-repeat;\n}\n\n.changeover button,\n.handoff-btn,\n.new-game-btn {\n    display: flex;\n    color: var(--color-5);\n    padding: 2px 8px;\n    border: 1px solid var(--color-2);\n    border-radius: 10px;\n    background-color: var(--color-1);\n    transition: transform 300ms ease-out;\n}\n\n.changeover button,\n.handoff-btn div {\n    display: flex;\n    align-items: center;\n}\n\n.changeover button img,\n.handoff-btn img {\n    width: clamp(25px, 5vw, 45px);\n}\n\n.changeover button:hover,\n.handoff-btn:hover,\n.new-game-btn:hover {\n    cursor: pointer;\n    transform: scale(1.05);\n    background-color: var(--color-2);\n}\n\n.gg {\n    margin: 50px 0px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    color: var(--color-5);\n    font-size: 2rem;\n}\n\n.gg div:nth-child(1) {\n    font-size: 2.5rem;\n}\n\n.gg div:nth-child(2) {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: fit-content;\n    color: var(--color-5);\n    background-color: var(--color-1);\n    font-size: 2rem;\n    padding: 10px;\n}\n\n.gg div:nth-child(2) img {\n    width: clamp(25px, 5vw, 45px);\n}\n\n.gg + button {\n    color: var(--color-5);\n    padding: 10px 30px;\n    border: 1px solid var(--color-2);\n    border-radius: 10px;\n    background-color: var(--color-1);\n    font-size: 1.5rem;\n    transition: transform 300ms ease-out;\n}\n\n.gg + button:hover {\n    cursor: pointer;\n    transform: scale(1.05);\n    background-color: var(--color-2);\n}\n\nfooter a {\n    color: var(--color-5);\n}"],"sourceRoot":""}]);
